import { Contract } from "@ethersproject/contracts";
import { useCall, useCalls } from "@usedapp/core";
import React, { useEffect, useState } from "react";

import { Body, Container, Image, StyledTable } from "./components";
import logo from "./infura.png";

import { addresses, abis } from "@my-app/contracts";

function App() {
  // Read more about useDapp on https://usedapp.io/

  let { error: getParticipantsError, value: participants } =
    useCall({
      contract: new Contract(addresses.competition, abis.competition),
      method: "getParticipants",
      args: [],
    }) ?? {};

  participants = participants
    ? participants
    : [
        "0x0000000000000000000000000000000000000000",
        "0x0000000000000000000000000000000000000000",
      ];

  const getAddresses = [];
  for (let i = 0; i < participants.length; i++) {
    for (let j = 0; j < participants[i].length; j++) {
      getAddresses.push(participants[i][j]);
    }
  }

  // Look up the points for each participant
  const preparePointsMultiCall = [];
  const pepareJailMultiCall = [];
  const pepareNamelMultiCall = [];

  for (let i = 0; i < getAddresses.length; i++) {
    preparePointsMultiCall.push({
      contract: new Contract(addresses.competition, abis.competition),
      method: "getParticipantPoints",
      args: [getAddresses[i]],
    });

    pepareJailMultiCall.push({
      contract: new Contract(addresses.competition, abis.competition),
      method: "isInJail",
      args: [getAddresses[i]],
    });

    pepareNamelMultiCall.push({
      contract: new Contract(addresses.competition, abis.competition),
      method: "getParticipantName",
      args: [getAddresses[i]],
    });
  }

  let pointsMultiCall = useCalls(preparePointsMultiCall) ?? {};
  let jailMultiCall = useCalls(pepareJailMultiCall) ?? {};
  let nameMultiCall = useCalls(pepareNamelMultiCall) ?? {};

  let collectPoints = [];
  for (let i = 0; i < pointsMultiCall.length; i++) {
    if (pointsMultiCall[i] && !pointsMultiCall[i].error) {
      collectPoints.push(pointsMultiCall[i].value);
    } else {
      continue; // Too many errors
    }
  }

  let inJail = [];
  for (let i = 0; i < jailMultiCall.length; i++) {
    if (jailMultiCall[i] && !jailMultiCall[i].error) {
      inJail.push(jailMultiCall[i].value);
    } else {
      continue; // Too many errors
    }
  }

  let names = [];
  console.log(nameMultiCall);
  for (let i = 0; i < nameMultiCall.length; i++) {
    if (nameMultiCall[i] && !nameMultiCall[i].error) {
      names.push(nameMultiCall[i].value);
    } else {
      continue; // Too many errors
    }
  }

  const data = [];
  if (getAddresses.length === collectPoints.length) {
    for (let i = 0; i < getAddresses.length; i++) {
      console.log(getAddresses[i], collectPoints[i].toString(), inJail[i]);
      data.push({
        address: getAddresses[i],
        points: collectPoints[i].toString(),
        jail: inJail[i].toString(),
        name: names[i],
      });
    }
  }

  return (
    <Container>
      <Body>
        <StyledTable>
          <tbody>
            <tr>
              <th>Competitor</th>
              <th>Name</th>
              <th>Jail</th>
              <th>Points</th>
            </tr>
            {data.map((val, key) => {
              return (
                <tr key={key}>
                  <td>{val.address}</td>
                  <td>{val.name}</td>
                  <td>{val.jail}</td>
                  <td>{val.points}</td>
                </tr>
              );
            })}
          </tbody>
        </StyledTable>
        <Image src={logo} alt="ethereum-logo" />
      </Body>
    </Container>
  );
}

export default App;
